import { appSetting } from 'src/app/app-setting';
import { priceModel } from './../../model/priceModel';
import { Observable } from "rxjs";
import { HttpHeaders } from "@angular/common/http";
import { HttpClient } from "@angular/common/http";
import { Injectable } from '@angular/core';
import Swal from 'sweetalert2';

@Injectable({
  providedIn: 'root'
})
export class UnitService {
  private url = `${this.appSetting.http}://${this.appSetting.apiPort}/api/priceModels`;
  private httpOptions = {
    headers: new HttpHeaders({
      "Content-Type": "application/json"
    })
  };
  constructor(private http: HttpClient,private appSetting:appSetting) {}
  get(id: number): Observable<priceModel[]> {
    const searchUrl = `${this.url}/${id}`;
    return this.http.get<priceModel[]>(searchUrl);
  }
  post(data: priceModel){
    var obj;
    let x:priceModel;
    this.http.post(this.url, data, this.httpOptions).subscribe(
      res => {
        obj=res;
        console.log(res);
      },
      err => {
        console.log(err);
      },
      () => {
        x=Object.assign(obj);
        Swal.fire("Success","Your Process is successfully","success");
      }
    );
  } 
  delete(id: number): void {
    var data = null;
    var xhr = new XMLHttpRequest();
    xhr.withCredentials = false;

    xhr.addEventListener("readystatechange", function() {
      if (this.readyState === this.DONE) {
        console.log(this.responseText);
      }
    });

    xhr.open("DELETE", this.url + "/" + id);
    xhr.send(data);
    Swal.fire("Success","Your Process is successfully","success");
  }
}

