import { userOrderTrackingModel } from './../../model/userOrderTrackingModel';
import { appSetting } from 'src/app/app-setting';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UserOrderTrackingService {
  private url = `${this.appSetting.http}://${this.appSetting.apiPort}/api/userOrderTracking`;
  private Delivery_url = `${this.appSetting.http}://${this.appSetting.apiPort}/api/userDeliveryTracking`;
constructor(private http: HttpClient,private appSetting:appSetting) {}

getAll(): Observable<userOrderTrackingModel[]>{
  
  return this.http.get<userOrderTrackingModel[]>(this.url);
}
getDelivery(): Observable<userOrderTrackingModel[]>{
  
  return this.http.get<userOrderTrackingModel[]>(this.Delivery_url);
}
get(id): Observable<userOrderTrackingModel[]>{
  let url=this.url+`/${id}`;
  return this.http.get<userOrderTrackingModel[]>(url);
}
}
