import { userModel } from './../../model/userModel';
import { appSetting } from 'src/app/app-setting';
import { Injectable } from '@angular/core';
import { Observable } from "rxjs";
import { HttpHeaders, HttpErrorResponse } from "@angular/common/http";
import { HttpClient } from "@angular/common/http";
import Swal from 'sweetalert2';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  private url = `${this.appSetting.http}://${this.appSetting.apiPort}/api/userModels`;
  private httpOptions = {
    headers: new HttpHeaders({
      "Content-Type": "application/json"
    })
  };
  constructor(private http: HttpClient,private appSetting:appSetting) {}


  get(id:number): Observable<userModel[]> {
    const searchUrl = `${this.url}/${id}`;
    return this.http.get<userModel[]>(searchUrl);
  }
  getSingle(id: number): Observable<userModel> {
    const searchUrl = `${this.url}/${id}`;
    return this.http.get<userModel>(searchUrl);
  }

  post(data: userModel){
    var obj;
    Swal.fire({
      title: 'Please wait',
      html: 'System is processing for your request',
      onBeforeOpen: () => {
        Swal.showLoading()}});
    this.http.post(this.url, data, this.httpOptions).subscribe(
      res => {
        obj=res;
      },
      err => {
        console.log(err);
        let x:HttpErrorResponse=Object.assign(err);
         Swal.hideLoading();
        Swal.fire(
          'Opps',x.error['Message'].toString(),
          'error'
        )
        
        
      },
      () => {
       console.log(obj);
       let x:userModel=Object.assign(obj);
       this.appSetting.sessionUser=x;
        Swal.hideLoading();
        this.appSetting.signUp=0;this.appSetting.otp=1
       Swal.fire(
         'Success','Your account has been successfully created.',
         'success'
       )
      
      }
      
    );
    
  }

  put(data: userModel): void {
    const searchUrl = `${this.url}/${data.userID}`;
    this.http.put(searchUrl, data, this.httpOptions).subscribe(
      res => {
        console.log(res);
      },
      err => {
        console.log(err);
      }
    );
  }

  delete(main: number): void {
    var data = null;
    var xhr = new XMLHttpRequest();
    xhr.withCredentials = false;

    xhr.addEventListener("readystatechange", function() {
      if (this.readyState === this.DONE) {
        console.log(this.responseText);
      }
    });

    xhr.open("DELETE", this.url +"/"+main);
    xhr.send(data);
    Swal.fire("Success","Your Process is successfully","success");
  }
}